.wave {
  top: 0;
  right: 0;
  z-index: -1;
  height: 100vh;
  position: fixed;
}
.wave-lg {
  height: 200vh;
  position: absolute;
}
@media screen and (max-width: 992px){
	.wave{
		display: none;
	}
}

.navbar-brand > span {
  color: #0d6efd;
  display: inline-block;
}

.bd-placeholder-img {
  font-size: 1.125rem;
  text-anchor: middle;
  user-select: none;
  -moz-user-select: none;
  -webkit-user-select: none;
}

.w-badge {
  width: 100px;
}

.bi {
  fill: currentColor;
  vertical-align: -.125em;
}

.form-label {
  font-weight: 500;
}

.card-cover {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
}
.card-cover > .d-flex {
  cursor: pointer;
  background: linear-gradient(rgba(0,0,0,0.25), rgba(0,0,0,.75));
}
.card-cover:hover > .d-flex {
  background: linear-gradient(rgba(0,0,0,.5), rgba(0,0,0,1));
}

.embed-responsive {
  position: relative;
  display: block;
  width: 100%;
  padding: 0;
  overflow: hidden;
}
.embed-responsive:before {
  display: block;
  content: "";
}
.embed-responsive .embed-responsive-item,
.embed-responsive iframe {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: 0;
}
.embed-responsive-16by9:before {
  padding-top: percentage(9 / 16);
}
.embed-responsive-16by9:before {
  padding-top: 56.25%;
}
.embed-responsive::before {
  display: block;
  content: "";
}

.navbar-nav .nav-link {
  border-bottom: .25rem solid transparent;
}
.navbar-nav .active {
  border-bottom-color: #0d6efd;
}